import dealWithTime from '../../src/util/dealWithTime.js'
import afterLeave from '../../components/afterLeave.vue'
import afterSub from '../../components/afterSub.vue'
import countDownCircle from '../../components/countDownCircle.vue'
import {
  getWeeklyStatus
} from '../../api'
import {
  mapState,
  mapMutaions
} from 'vuex'

export default {
  computed: {
    ...mapState(['week', 'user'])
  },
  data() {
    return {
      leftTime: {
        left_days: 0,
        left_hours: 0,
        left_minutes: 0,
        left_seconds: 0,
      },
    }
  },
  components: {
    afterLeave,
    afterSub,
    countDownCircle
  },
  onLoad() {
    // 获取剩余时间
    this.getLeftTime();
    //请求用户周报数据
    getWeeklyStatus(this.user.userId).then(res => {
      console.log(res[1])
      if (res[1].data.infoCode === 500) {
        uni.showToast({
          icon: 'none',
          title: res[1].data.infoText
        })
      } else {
        this.user.status = res[1].data.status;
      }
    })
  },
  methods: {
    getLeftTime() {
      this.leftTime = dealWithTime();
    }
  }
}
